export const spiderWebFlavor = {
    name: 'SpiderWeb',
    description: 'Radial spider-web with varied branch count and web density',
    isArtistic: true,

    getFeaturesVariations: () => ({
        numberOfMainBranches: [4, 7, 17],
        ringSpacingFactor:   [0.05, 0.2, 0.5]
    }),

    drawCell(ctx, cell, size, rand){
        // ---------- layout constants (all fractions of size) ----------
        const center          = size * 0.5;
        const margin          = -size * 0.3; // negative to make the spider web stretched out
        const maxRadius       = center - margin;

        // ---------- style ----------
        ctx.strokeStyle  = '#000000';

        // ---------- scene parameters ----------
        const branches   = cell.numberOfMainBranches;
        const spacing    = cell.ringSpacingFactor * size;
        const angleStep  = (Math.PI * 2) / branches;
        const offset     = rand() * Math.PI * 2;           // random rotation

        // ---------- clear canvas ----------
        ctx.clearRect(0, 0, size, size);

        // ---------- radial spokes ----------
        ctx.lineWidth    = size * 0.002;
        for (let i = 0; i < branches; i++){
            const angle = offset + i * angleStep;
            const x = Math.cos(angle);
            const y = Math.sin(angle);

            ctx.beginPath();
            ctx.moveTo(center, center);
            ctx.lineTo(center + x * maxRadius, center + y * maxRadius);
            ctx.stroke();
        }

        // ---------- concentric curved â€œringsâ€ ----------
        ctx.lineWidth    = size * 0.003;
        for (let r = spacing; r < maxRadius; r += spacing){
            for (let i = 0; i < branches; i++){
                const a0 = offset + i * angleStep;
                const a1 = a0 + angleStep;

                ctx.beginPath();
                ctx.moveTo(center + Math.cos(a0) * r, center + Math.sin(a0) * r);
                ctx.quadraticCurveTo(center, center,
                    center + Math.cos(a1) * r, center + Math.sin(a1) * r);
                ctx.stroke();
            }
        }
    }
};
